* {
  margin: 0;
  padding: 0;
}

body {
  height: 95vh;
  padding: 20px;

  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: #342869;
}

.container {
  width: 80%;
  margin: 0 auto;
}

.app {
  background-image: url(../images/background.png);
  background-repeat: no-repeat;
  background-size: contain;
}

button {
  cursor: pointer;
}

.app,
.main,
.nav_left,
.nav_right {
  height: 100%;
}

.app,
.container,
.nav,
.main,
.nav_left,
.nav_right,
.logo,
.menu,
.search_bin,
.arrows,
.menu_list_wrapper,
.menu_list,
.search_field,
.product {
  display: flex;
}

.app,
.nav_left,
.nav_right,
.menu_list,
.product {
  flex-direction: column;
}