.main .container {
    flex-direction: column;
    height: 100%;
  }
  
  .main .slider {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 100%;
    width: 100%;
    overflow: hidden;
  }
  
  .product {
    min-width: 300px;
    width: 23%;
  
    border-radius: 40px;
    background-color: #fff;
  
    justify-content: space-between;
    overflow: hidden;
    flex: none;
    margin-right: 35px;
  }
  
  .product .image {
    text-align: center;
    height: 200px;
  
    overflow: hidden;
    margin-top: 30px;
  
    margin-left: 10px;
    margin-right: 10px;
  }
  
  .product .image img {
    max-width: 100%;
    max-height: 100%;
  }
  
  .product .description {
    background-color: #342869;
    color: #fff;
    border-radius: 40px;
  
    margin: 5px;
    padding: 35px;
  
    font-size: 16px;
    font-weight: normal;
  
    position: relative;
  }
  
  .product .description h3 {
    font-size: 22px;
    max-height: 25px;
    margin-bottom: 20px;
  
    overflow: hidden;
  }

  .product .description p {
    max-height: 110px;
    min-height: 110px;
    overflow: hidden;
  }
  
  .product .description .cost {
    margin-top: 15px;
    font-size: 20px;
  }
  
  .button_add {
    display: flex;
    position: absolute;
    justify-content: center;
    align-items: center;
  
    width: 51px;
    height: 51px;
  
    background-color: #574A97;
    border-radius: 50%;
    border: none;
  
    bottom: 20px;
    right: 20px;
  }
  
  .button_add_icon1,
  .button_add_icon2 {
    width: 20px;
    height: 2px;
    background-color: #fff;
  }
  
  .button_add_icon2 {
    transform: rotate(90deg);
    position: absolute;
}